<template>
  <div>
    <fTitle>
      <template #title>Notification 通知</template>
      <template #introduce>悬浮出现在页面角落，显示全局的通知提醒消息。</template>
    </fTitle>
    <sTitle>
      <template #title>基本用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="10">
<div class="grid-content">    <el-button

    plain
    @click="open1">
    可自动关闭
  </el-button></div>
    </el-col>
    <el-col :span="10">
<div class="grid-content"> <el-button
    plain
    @click="open2">
    不会自动关闭
    </el-button></div>
    </el-col>
  </el-row>
  <el-row type="flex" justify="space-around">
    <el-col :span="5" v-for="item in typeList" :key="item.type">
<div class="grid-content">
  <el-button
    plain
    @click="open3(item)">
    {{ item.type+'按钮' }}
  </el-button></div>
    </el-col>
  </el-row>
  <el-row type="flex" justify="space-around">
    <el-col :span="5" v-for="item in positionList" :key="item.type">
<div class="grid-content">
  <el-button
    plain
    @click="open4(item)">
    {{ item.text.slice(0,3) }}
  </el-button>
</div>
    </el-col>
  </el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      typeList: [
        {
          type: 'success',
          text: '这是一条成功的提示消息'
        },
        {
          type: 'warning',
          text: '这是一条警告的提示消息'
        },
        {
          type: 'info',
          text: '这是一条消息的提示消息'
        },
        {
          type: 'error',
          text: '这是一条错误的提示消息'
        }
      ],
      positionList: [
        {
          type: 'top-right',
          text: '右上角弹出的消息'
        },
        {
          type: 'bottom-right',
          text: '右下角弹出的消息'
        },
        {
          type: 'bottom-left',
          text: '左下角弹出的消息'
        },
        {
          type: 'top-left',
          text: '左上角弹出的消息'
        }
      ]
    }
  },
  methods: {
    open1 () {
      const h = this.$createElement

      this.$notify({
        title: '标题名称',
        message: h('i', { style: 'color: teal' }, '这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案')
      })
    },

    open2 () {
      this.$notify({
        title: '提示',
        message: '这是一条不会自动关闭的消息',
        duration: 0
      })
    },
    open3 (item) {
      this.$notify({
        title: item.type,
        message: item.text,
        type: item.type
      })
    },
    open4 (item) {
      this.$notify({
        title: '自定义位置',
        message: item.text,
        position: item.type
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }}
</style>
